import { Component, NgZone } from '@angular/core';
import { NavController, NavParams, AlertController, ViewController, LoadingController } from 'ionic-angular';
import { Connect } from '../../providers/connect';


@Component({
  selector: 'page-showroom',
  templateUrl: 'showroom.html',
})
export class ShowroomPage {

  public infrom : any = '';
  public into : any = '';
  public exfrom : any = '';
  public exto : any = '';
  public batch : any = '';
  public loc : any = '';
  public ponum : any = '';
  public mode : any = 0;
  public results : any[];

  constructor(public navCtrl: NavController, public navParams: NavParams, private zone: NgZone, public alertCtrl: AlertController, public viewCtrl: ViewController, public loadingCtrl: LoadingController, public connect: Connect) {
  }

  ionViewDidLoad() {
     this.mode=0;
  }

 search() {
     let that=this;
            let loader = this.loadingCtrl.create({
	      content: "Loading..."
	    });
	    loader.present();

        let s = '~';
        let url = 'dosearch.php?params='+this.infrom + s + this.into + s + this.exfrom + s + this.exto + s + encodeURIComponent(this.batch) + s + encodeURIComponent(this.loc) + s + this.ponum;
        this.connect.getList(url).subscribe(
         data => {
           loader.dismiss();
           that.zone.run(() => {
             that.results = data.results;             
             that.mode=1;
           });
         },
         err => {
            loader.dismiss();
            this.connect.logError(err)
         }
        ); 

 } 

}



