import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { PickPage } from '../pick/pick';
import { ItemPage } from '../item/item';
import { ShowroomPage } from '../showroom/showroom';


@Component({
  selector: 'page-menu',
  templateUrl: 'menu.html',
})
export class MenuPage {

  me : any = '';
  username : any = '';
  user : any = '';
  password : any = '';
  error: any = '';
  mode : any = 0;

  constructor(public nav: NavController, public navParams: NavParams, public connect: Connect) {
  }

  ionViewDidLoad() {
     var u = window.localStorage.getItem('me');
     if (!u) u='';
    if (u.length) {
       this.me = u;
       this.username=window.localStorage.getItem('username');
       this.mode=1;
    }
    else  {
      this.mode=0;
      this.me='';
    }

  }


 goodsin() {
    this.nav.push(PickPage);
 }


 reports() {
    this.nav.push(ShowroomPage);
 }

 products() {
    this.nav.push(ItemPage);
 }

  login() {
    let that = this;
    this.connect.getList('userlogin.php?params='+encodeURI(this.user+'~'+this.password)).subscribe(
          data => {
            if (data.success) {
              that.me = data.me;
              that.username = data.username;
              that.mode=1;
              window.localStorage.setItem('me',that.me);
              window.localStorage.setItem('username',that.username);
            }
            else {
                that.error = data.error; 
            }
          },
          err => this.connect.logError(err)
        );
  }


 logout() {
    this.user='';
    this.password='';
    this.mode=0;
    window.localStorage.setItem('me','');
 }


}



