import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { PickerPage } from '../picker/picker';

@Component({
  selector: 'page-pick',
  templateUrl: 'pick.html',
})
export class PickPage {

 srch : any = '';
 sups : any = [];
 porders : any = [];
 supplier: any = '';
 mode : any = 0;
 mode2 : any = 0;
  constructor(public nav: NavController, public navParams: NavParams, private connect: Connect, public loadingCtrl : LoadingController, private zone: NgZone, public alertCtrl: AlertController) {
     this.srch='';
     this.mode=0; 
     
  }

  ionViewDidLoad() {
  }


 goPord(a) {
   this.mode=0;
   this.srch='';
   this.nav.push(PickerPage, {'id' : a});
 }
 
 doSearch() {
        let that=this;
        let url = 'getposearch.php?srch='+encodeURIComponent(this.srch);
        this.connect.getList(url).subscribe(
         data => {
           that.zone.run(() => {
             if (data.success) {
                that.porders = data.porders;
                if (that.porders.length==1) {
                  that.goPord(that.porders[0].id);
                }
                else that.mode=3; 
             }

            });
         },
         err => {
            this.connect.logError(err)
         }
        ); 


 }

 


 supsrch() {
   this.mode2=0;
   this.mode=1;
 }

 goback() {
   this.mode=0;
 }

 gobrand(a) {
       let that=this;
       this.mode2=0;
        let url = 'getsups.php?l='+a;
        this.connect.getList(url).subscribe(
         data => {
           that.zone.run(() => {
              that.mode2=1;
              that.sups=data.sups;
           });
         },
         err => {
            this.connect.logError(err)
         }
        ); 


 }

 choosebrand(a) {
       let that=this;
        let url = 'getpos.php?srch=&sup='+encodeURIComponent(a);
        this.connect.getList(url).subscribe(
         data => {
           that.zone.run(() => {
              that.porders=data.porders;
              that.mode=3;
           });
         },
         err => {
            this.connect.logError(err)
         }
        ); 
   
 }

}



