import { Component, NgZone, ChangeDetectorRef} from '@angular/core';
import { NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';

/**
 * Generated class for the StockpPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-picker',
  templateUrl: 'picker.html',
})
export class PickerPage {

  prodlist : any[];
  prods : any[];
  id : any = '';
  pstatus : any;
  thissku : any = '';
  thisname : any = '';
  thisqty : any = '';
  thisid : any;
  thisloc : any;
  thisrec : any = 0;
  thisbatch : any;
  thisexpiry : any;
  unexpected : any = 0;
 thisline : any = 0;
  barsku : any = '';
  allgood: any = 0;
  nw : any;
  constructor(public navCtrl: NavController, public navParams: NavParams, private connect: Connect, public loadingCtrl : LoadingController, private zone: NgZone, public alertCtrl: AlertController, public cdr: ChangeDetectorRef, public barcodeScanner : BarcodeScanner) {
     this.id=this.navParams.get("id");
     this.pstatus=0;
     this.prodlist=[];
     this.barsku='';
     this.allgood = 0;
     this.nw=[];
  }

  ionViewDidLoad() {
        let that=this;
            let loader = this.loadingCtrl.create({
	      content: "Loading..."
	    });
	    loader.present();

        let url = 'pickdetails.php?id='+this.id;
        this.connect.getList(url).subscribe(
         data => {
           loader.dismiss();
           that.zone.run(() => {
             that.prodlist = data.prodlist;             
           });
         },
         err => {
            loader.dismiss();
            this.connect.logError(err)
         }
        ); 
     
  }

  lookup() {
     this.findProd(this.barsku);
  }

  scanBar() {
    this.barcodeScanner.scan().then((barcodeData) => {
       if (!barcodeData.cancelled) {
          this.findProd(barcodeData.text);
       }
    }, (err) => {
    });
  }
  
  findProd(s) {
        let that=this;
           

        let url = 'getprodsearch.php?srch='+s;
        this.connect.getList(url).subscribe(
         data => {
            that.zone.run(() => {
             console.log(data);
             if (data.success) {
              that.prods = data.prods;
                if (that.prods.length==1) {
                  that.doProduct(that.prods[0]);
                }
                else that.pstatus=3; 
 
             }
             
           })
         },
         err => {
           this.connect.logError(err)
         }
        ); 
  }


  doProduct(data)
  { let that= this;
              let found  = 0;
                  that.thissku = data.sku;
                  that.thisname = data.name;
                  that.thisid = data.id;
  that.thisloc = data.loc;
  that.thisbatch = data.batch;
  that.thisline = data.line;
  that.thisexpiry = data.expiry;
  that.unexpected=0;
                  that.barsku='';
                  that.thisqty='';

               for ( var i in that.prodlist) 
                if (that.prodlist[i].id == data.id) 
                  found = 1;
               if (found ==0) {
                  this.nw = {"id" : data.id, "rec" : 0, "line" : 0, "sku" : data.sku, "name" : data.name, "size" : data.size, "expected" : 0, "counted" : 0, "backcolor" : "#FFDDDD", "loc" : "", "expiry" : "", "batch" : ""};
                  this.unexpected=1;
                }
 
               that.pstatus=1;
  }

  cancelit()
  { this.pstatus=0;
  }
  
  saveit()
  { this.pstatus=2;
  }

  saveit2()
  {  let that=this;
     let q = 1;
     let found = 0;
     if (this.thisqty.length) q=parseInt(this.thisqty);
     that.zone.run(() => {
       for (var i in that.prodlist) {
         if (that.prodlist[i].id == that.thisid) {
            found = 1;
            that.prodlist[i].counted += q;        
            that.prodlist[i].batch = that.thisbatch;
            that.prodlist[i].expiry = that.thisexpiry;
            that.prodlist[i].loc = that.thisloc;
         }
         if (that.prodlist[i].counted == that.prodlist[i].expected)
              that.prodlist[i].backcolor = '#DDFFDD';
         else {
           if (that.prodlist[i].counted > that.prodlist[i].expected)
              that.prodlist[i].backcolor = '#FFDDDD';
           else
              that.prodlist[i].backcolor = '#EEEEEE';
         }
       }
       if (!found) {
           that.nw['counted']=q;
           that.nw['batch']=that.thisbatch;
           that.nw['loc']=that.thisloc;
           that.nw['expiry']=that.thisexpiry;
           that.prodlist.push(that.nw);         
       }
       this.pstatus=0;
     });
  }

  kin(a)
  { this.thisqty = '' + this.thisqty + a;
  }

  allcomplete()
  {  

       let that=this;
            let loader = this.loadingCtrl.create({
	      content: "Saving..."
	    });
	    loader.present();

        let url = 'pickcomplete.php?id='+this.id+'&me='+window.localStorage.getItem('me')+'&cart='+encodeURIComponent(JSON.stringify(this.prodlist));
        this.connect.getList(url).subscribe(
         data => {
           loader.dismiss();
           that.zone.run(() => {
             that.navCtrl.pop();             
           });
         },
         err => {
            loader.dismiss();
            this.connect.logError(err)
         }
        ); 
  }

}



