import { Component, NgZone, ChangeDetectorRef} from '@angular/core';
import { NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';

/**
 * Generated class for the StockpPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-stockp',
  templateUrl: 'stockp.html',
})
export class StockpPage {

  prodlist : any[];
  orderno : any = '';
  pstatus : any;
  thissku : any = '';
  thisname : any = '';
  thisqty : any = '';
  thisid : any;
  thisloc : any;
  barsku : any = '';

  constructor(public navCtrl: NavController, public navParams: NavParams, private connect: Connect, public loadingCtrl : LoadingController, private zone: NgZone, public alertCtrl: AlertController, public cdr: ChangeDetectorRef, public barcodeScanner : BarcodeScanner) {
     this.orderno=this.navParams.get("orderno");
     this.pstatus=0;
     this.prodlist=[];
     this.barsku='';
  }

  ionViewDidLoad() {
  }

  lookup() {
     this.findProd(this.barsku);
  }

  scanBar() {
    this.barcodeScanner.scan().then((barcodeData) => {
       if (!barcodeData.cancelled) {
          this.findProd(barcodeData.text);
       }
    }, (err) => {
    });
  }
  
  findProd(s) {
        let that=this;
            let loader = this.loadingCtrl.create({
	      content: "Searching..."
	    });
	    loader.present();

        let url = 'spitem.php?code='+s;
        this.connect.getList(url).subscribe(
         data => {
           loader.dismiss();
           that.zone.run(() => {
             if (data.result > 0) {
                that.thissku = data.SKU;
                that.thisname = data.name;
                that.thisid = data.id;
                that.barsku='';
                that.thisloc='';
                that.thisqty='';
                that.pstatus=1;
             }
             
           });
         },
         err => {
            loader.dismiss();
            this.connect.logError(err)
         }
        ); 
  }

  completeOrd() {
        let that=this;
            let loader = this.loadingCtrl.create({
	      content: "Saving..."
	    });
	    loader.present();

        let url = 'completeso.php?id='+this.orderno+'&items='+JSON.stringify(this.prodlist);
        this.connect.getList(url).subscribe(
         data => {
           loader.dismiss();
           that.zone.run(() => {
             that.navCtrl.pop();
           });
         },
         err => {
            loader.dismiss();
            this.connect.logError(err)
         }
        ); 
  }


  delProd(id)
  { let nw = [];
    let sz = this.prodlist.length;
    for (let i=0; i<sz; i++) 
      if (this.prodlist[i].id != id)
         nw.push(this.prodlist[i]);
     this.zone.run(() => {
       this.prodlist=nw;;
     });
  }

  cancelit()
  { this.pstatus=0;
  }
  
  saveit()
  {  var nw = {'id' : this.thisid, 'sku' : this.thissku, 'name'  : this.thisname, 'qty' : this.thisqty, 'loc' : this.thisloc};
     let that=this;
     that.zone.run(() => {
       this.prodlist.push(nw);
       this.pstatus=0;
     });
  }

  
}



