import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';

/**
 * Generated class for the VanPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-van',
  templateUrl: 'van.html',
})
export class VanPage {

  public past : any [];
  public speedo : any = '';
  public speedo2: any = '';
  public orderno : any = '';
  public sts : any = 0;
  public dtls : any = '';
  constructor(public navCtrl: NavController, public navParams: NavParams, private connect: Connect, private zone: NgZone,  public cdr: ChangeDetectorRef) {
    this.sts=0;
    this.speedo='';
    this.speedo2='';
    this.orderno='';
    this.dtls='';
    this.past=[];
  }

  ionViewDidLoad() {
    this.vanStatus(0);
  }

  vanStatus(a) {
    if (a==3) 
      this.speedo=this.speedo2;

    let url = 'vanstatus.php?mode='+this.sts+'&orderno='+this.orderno+'&speedo='+this.speedo;
console.log(url);
    let that = this;
        this.connect.getList(url).subscribe(
         data => {
           that.zone.run(() => {
             console.log(data);
             that.sts = data.sts;
             that.past = data.past;
             that.dtls=data.dtls;              
           });
         },
         err => {
            this.connect.logError(err)
         }
        ); 



  }

}



