import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';
import { Connect } from '../../providers/connect';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { StockPage } from '../stock/stock';

@Component({
  selector: 'page-item',
  templateUrl: 'item.html',
})
export class ItemPage {

 srch : any = '';
 brands : any = [];
 prods : any = [];
 brand: any = '';
 mode : any = 0;
 mode2 : any = 0;
  constructor(public nav: NavController, public navParams: NavParams, private connect: Connect, public loadingCtrl : LoadingController, private zone: NgZone, public alertCtrl: AlertController, public barcodeScanner : BarcodeScanner) {
     this.srch='';
     this.mode=0; 
     
  }

  ionViewDidLoad() {
  }

  doScan() {
    this.barcodeScanner.scan().then((barcodeData) => {
       if (!barcodeData.cancelled) {
          this.checkCode(barcodeData.text);
       }
    }, (err) => {
    });
  }


 doSearch() {
        let that=this;
        let url = 'getprodsearch.php?srch='+encodeURIComponent(this.srch);
        this.connect.getList(url).subscribe(
         data => {
           that.zone.run(() => {
             if (data.success) {
                that.prods = data.prods;
                if (that.prods.length==1) {
                  that.goProd(that.prods[0].id);
                }
                else that.mode=3; 
             }

            });
         },
         err => {
            this.connect.logError(err)
         }
        ); 


 }

 checkCode(a) {
          let that=this;
        let url = 'getbarlook.php?code='+a;
        this.connect.getList(url).subscribe(
         data => {
           that.zone.run(() => {
             if (data.success) 
               that.goProd(data.id);
           });
         },
         err => {
            this.connect.logError(err)
         }
        ); 
 }

 goProd(id)
 { this.nav.push(StockPage,{'id' : id});
 }


 brandsrch() {
   this.mode2=0;
   this.mode=1;
 }

 goback() {
   this.mode=0;
 }

 gobrand(a) {
       let that=this;
       this.mode2=0;
        let url = 'getbrands.php?l='+a;
        this.connect.getList(url).subscribe(
         data => {
           that.zone.run(() => {
              that.mode2=1;
              that.brands=data.brands;
           });
         },
         err => {
            this.connect.logError(err)
         }
        ); 


 }

 choosebrand(a) {
       let that=this;
        let url = 'getprods.php?srch=&brand='+encodeURIComponent(a);
        this.connect.getList(url).subscribe(
         data => {
           that.zone.run(() => {
              that.prods=data.prods;
              that.mode=3;
           });
         },
         err => {
            this.connect.logError(err)
         }
        ); 

   
 }

}



