import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { Camera } from '@ionic-native/camera';
import { FileTransfer } from '@ionic-native/file-transfer';
import { HttpModule } from '@angular/http';
import { BarcodeScanner } from '@ionic-native/barcode-scanner';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { MenuPage } from '../pages/menu/menu';
import { PickPage } from '../pages/pick/pick';
import { ItemPage } from '../pages/item/item';
import { StockPage } from '../pages/stock/stock';
import { StockpPage } from '../pages/stockp/stockp';
import { PhotosPage } from '../pages/photos/photos';
import { ShowroomPage } from '../pages/showroom/showroom';
import { VanPage } from '../pages/van/van';
import { PickerPage } from '../pages/picker/picker';

import { Connect } from '../providers/connect';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    StockPage,
    StockpPage,
    PickPage,
    ItemPage,
    PickerPage,
    VanPage,
    ShowroomPage,
    PhotosPage,
    MenuPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    PickPage,
    StockPage,
    StockpPage,
    ItemPage,
    ShowroomPage,
    PickerPage,
    VanPage,
    PhotosPage,
    MenuPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    BarcodeScanner,
    Camera,
    FileTransfer,
    Connect,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



