import { Component, NgZone, ChangeDetectorRef } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { Connect } from '../../providers/connect';


@Component({
  selector: 'page-stock',
  templateUrl: 'stock.html',
})
export class StockPage {

 public id : any = 0;
 code : any = '';
 desc : any = '';
 barcode : any = '';
 size : any = '';
 results: any[];

  constructor(public navCtrl: NavController, public navParams: NavParams, public connect : Connect, private zone: NgZone, public cdr: ChangeDetectorRef) {
   
  }

  ionViewDidLoad() {
     this.id = this.navParams.get('id');
     this.refresh();
  }

  refresh() {
    let that = this;
    let url = 'getprod.php?id='+this.id;
    this.connect.getList(url).subscribe(
      data => {
        that.zone.run(() => {
          that.code = data.code;
	  that.desc = data.desc;
          that.barcode = data.barcode;
          that.size = data.size;
          that.results = data.results;
          that.cdr.markForCheck();
        });
      },
      err => this.connect.logError(err)
    );
  }

}



